.benefits {
    width: 100%;
    margin-top: -1px;
    background-image:linear-gradient(to bottom, $title-text-color 67%, $primary-white-color 67%);

    @media screen and (min-width: 768px) {
        background-image:linear-gradient(to bottom, $title-text-color 50%, $primary-white-color 50%); 
        padding-top: 35px;
    }
}

.benefits__container {
    @extend %container;
    position: relative;
    z-index: 1;
    margin-bottom: 0;
}

.benefits__title {
    width: 62%;
    margin-bottom: 30px;
    font-weight: 800;
    font-size: 20px;
    line-height: 1.36;
    letter-spacing: 0.03em;
    color: $primary-white-color;

    @media screen and (min-width:480px){
        width: 180px;
    }

    @media screen and (min-width:768px){
        width: 688px;
        font-size: 26px;
        line-height: 1.3;
        text-align: center;
    }

     @media screen and (min-width:1366px) {

        position: absolute;
        width: 1px;
        height: 1px;
        margin: -1px;
        border: 0;
        padding: 0;

        white-space: nowrap;
        clip-path: inset(100%);
        clip: rect(0 0 0 0);
        overflow: hidden;
    }
}

.no-wrap {
    white-space: nowrap;
}

.benefits__list {
    width: 100%;
    padding: 35px 22px;

    background-color: $primary-white-color;
    box-shadow: -5px 8px 26px rgba(0, 0, 0, 0.04);
    
    counter-reset: item;

    @media screen and (min-width: 480px){
        max-width: 280px;
        padding: 35px 22px;
    }

    @media screen and (min-width: 768px){
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        max-width: 688px;
        padding: 40px 98px;
    }

     @media screen and (min-width: 1366px){
        max-width: 1314px;
        padding: 67px 113px;
    }
}

.benefits__item {
    &:not(:last-child) {
        margin-bottom: 30px;
    }

    @media screen and (min-width:768px) {
        width: 236px;
        &:not(:last-child) {
            margin-bottom: 0;
        }

        &:not(:nth-child(n+5)) {
            margin-bottom: 20px;
        }
    }

    @media screen and (min-width: 1366px) {
        width: 336px;

        &:not(:nth-child(n+5)) {
            margin-bottom: 0;
        }

        &:nth-child(-n+3) {
            margin-bottom: 30px;
        }
    }
}


.benefits__text {
    font-weight: 700;
    font-size: 14px;
    line-height: 1.43;
    letter-spacing: 0.08em;
    color: $title-text-color;

    &::before {
        display: block;
        margin-bottom: 10px;

        font-weight: 700;
        font-size: 14px;
        line-height: 1.65;
        letter-spacing: 0.08em;
        color: $accent-color;

        counter-increment: item;
        content: "— 0" counter(item);
    }

    @media screen and (min-width: 480px) {
        max-width: 236px;
    }

    @media screen and (min-width: 768px) {
        max-width: 236px;
        line-height: 1.71;
    }

    @media screen and (min-width: 1366px) {
        max-width: 336px;
        font-size: 20px;
        line-height: 1.2;
        
        &::before {
            font-size: 20px;
        }
    }
}
