
$primary-text-color: #b9b9b9;
$title-text-color: #111518;
$accent-color: #ff6b09;
$primary-white-color: #ffffff;

$accent-description: #3d3d3d;

$background-color-section: #f1f1f1;

$transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

$animation-image: animate-scale 2000ms cubic-bezier(0.4, 0, 0.2, 1);

@keyframes animate-scale {
    0% {
        transform: scale(0.7);
        opacity: 0;
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }
}